.App {
  background-image: url("assets/img/ToDoList.jpg");
  /* filter: sepia(50%) saturate(100%) brightness(70%) hue-rotate(180deg); */
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  min-height: 100%;
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  /* background-position: center center; */
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat Regular"), local("Nunito-Regular"),
    url(https://fonts.gstatic.com/s/nunito/v11/XRXV3I6Li01BKofINeaBTMnFcQ.woff2)
      format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA,
    U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215,
    U+FEFF, U+FFFD;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

div.wrapper {
  animation: 1.5s wrapper;
  overflow-y: hidden;
  height: 50%;
  margin-top: 100px;
}

@keyframes wrapper {
  from {
    margin-top: 100%;
    height: 300%;
  }

  to {
    margin-top: 10%;
    height: 100%;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
